.Hero {
    height: 100vh;
    width: 100vw;
    background: #A5D7E8;
    display: flex;
    align-items: center;
    justify-content: center;

}

.HeroSection {
    display: flex;
    justify-content: center;
    width: 1080px;
    align-items: center;
}

.HeroSectionOne {
    position: absolute;
    left: 200px;
    text-align: center;
    font-size: 1.3rem;
    font-weight: 900;
}

.HeroSectionTwo {
    position: absolute;
    right: 100px;
}

.RegisterButton {
    appearance: none;
    border-radius: 5px;
    height: 40px;
    margin-top: 35px;
    width: 180px;
    color: #A5D7E8;
    background-color: #19376D;
    border: 2px solid #0B2447;
}