.Navbar {
    background-color: #0B2447;
    display: flex;
    justify-content: center;
    width: 100vw;
    position: fixed;
    z-index: 5000;
    /* height: 60px; */
}

.NavContent{
    justify-content: space-between;
    display: flex;
    align-items: center;
    width: 1080px;
    /* border: 2px solid red; */
}

.NavButtons{
    display: flex;
    margin-right: 10px;
    align-items: center;
}

.Logo{
    color: #576CBC;
    font-weight: bolder;
    font-size: 1rem;
    margin: 5px 10px;
}

.Buttons{
    margin: 5px 10px;
    color: #A5D7E8;
    font-size: 1rem;
    
}

.Buttons:hover{
    border-bottom: 4px solid blueviolet;
    cursor: pointer;
}

.Home{
    color: #A5D7E8;
    border: 2px solid #A5D7E8;
    border-radius: 4px;
    margin: 5px 10px;
    font-size: 1rem;
    cursor: pointer;
    padding: 3px
}

.Login{
    background-color: #A5D7E8;
    color: #576CBC;
    padding: 3px;
    font-size: 1rem;
    cursor: pointer;
    border-radius: 3px;
}