.MentorDetails{
    height: 400px;
    position: absolute;
    border-radius:20px;
    padding: 20px;
    z-index: 250;
    left: 30%;
    display: flex;
    top: 20%;
    width: 500px;
    box-shadow: 2px 2px;
    color: #0B2447;
    background-color: #A5D7E8;
}

.SectionOne{
    
    display: flex;
    flex-direction: column;
    
    align-items: center;
    justify-content: space-around;
}

.SectionTwo{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.BookASession{
    font-size: 1.4rem;
    width: 70%;
    background-color: #0B2447;
    color: #A5D7E8;
    /* border: 2px solid brown; */
    cursor: pointer;
}