.Login{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    background-color: #A5D7E8;
}

.LoginCard{
    width: 280px;
    height: 350px;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
    box-shadow: 3px 3px;
}

.CardItem{
    margin: 10px 0;
}

.padding{
    padding: 10px;
    margin-top: 5px;
}

.LoginButton{
    width: 80%;
    height: 40px;
    padding: 5px;
    color: #A5D7E8;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    border-radius: 10px;
    font-size: 1.3rem;
    background-color: #576CBC;
}

.GoogleButton{
    width: 80%;
    height: 50px;
    padding: 5px;
    color: white;
    display: flex;
    margin-top: 10px;
    border-radius: 10px;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    font-size: 1.3rem;
    background-color: black;
}

