.MentorCard{
    /* border: 3px solid black; */
    border-radius: 5px;
    margin: 40px;
    background-color: #576CBC;
    display: flex;
    padding: 30px;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
}

.MentorData{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 1.5rem;
    color: #A5D7E8;
    
}


.MentorCard:hover{
    scale: 1.2;
    
    transition: all;
    transition-duration: 0.5s;
    cursor: pointer;
}
